'use client';
import { useEffect, useRef, useState } from 'react';
import io from 'socket.io-client';

const CameraPage = () => {
  const imgRef = useRef<HTMLImageElement | null>(null);
  const [error, setError] = useState<string | null>(null);
  const [isConnected, setIsConnected] = useState(false);
  const socketRef = useRef<any>(null);

  useEffect(() => {
    socketRef.current = io('http://160.22.122.122:8001');

    socketRef.current.on('connect', () => {
      console.log('Kết nối thành công');
      setIsConnected(true);
    });

    socketRef.current.on('disconnect', () => {
      console.log('Mất kết nối');
      setIsConnected(false);
    });

    socketRef.current.on('error', (err: any) => {
      console.error('Socket Error:', err);
      setError('Lỗi kết nối WebSocket.');
    });

    socketRef.current.on('video_frame', (frame: ArrayBuffer) => {
      const uint8Array = new Uint8Array(frame);
      const blob = new Blob([uint8Array], { type: 'image/jpeg' });
      const url = URL.createObjectURL(blob);

      if (imgRef.current) {
        imgRef.current.src = url;
      }
    });

    return () => {
      if (socketRef.current) {
        socketRef.current.close();
      }
    };
  }, []);

  return (
    <div className="container mx-auto p-4 bg-gray-900 text-white min-h-[90vh] rounded-xl shadow-lg">
      <div className="relative w-full h-[70vh] rounded-lg overflow-hidden">
        {error ? (
          <div className="text-center text-red-500">
            <p>{error}</p>
          </div>
        ) : isConnected ? (
          <div className="video-container">
            <img
              ref={imgRef}
              alt="Video stream"
              className="w-full h-full rounded-lg shadow-lg"
            />
          </div>
        ) : (
          <div className="text-center text-yellow-500">Đang kết nối...</div>
        )}
      </div>
    </div>
  );
};

export default CameraPage;
